  .main-desc {
    font-size: 2rem;
  }
}

@media screen and (max-width: 450px) {
  .main-desc {
    font-size: 1.2rem;
    margin-top: -30%;
  }
  .about-desc {
    text-align: justify;
    margin-top: 15%;
  }
}

/* Google Fonts - Import */

:root {
  --primary-color: #a0aec0;
  --secondary-color: #cbd5e0;
  --bg-color-1: #2d3748;
  --bg-color-2: #1a202c;
  --primary-font-size: 1.1rem;
  --primary-font: "Quicksand", sans-serif;
  --line-height: 1.5rem;
}

.container {
  padding: 1.5rem 10rem;
}

.shadow {
  box-shadow: 11px 11px 34px -12px rgba(0, 0, 0, 0.75);
}

.quicksand-family {
  font-family: "Quicksand", sans-serif;
}

.roboto-mono-family {
  font-family: "Roboto Mono", sans-serif;
}

.projects-container {
  background-color: var(--bg-color-2);
}

.projects {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: 1.5rem;
  gap: 1.5rem;
  justify-items: center;
  font-family: var(--primary-font);
  margin-top: 1rem;
}

.projects-title {
  display: block;
  text-align: center;
  font-family: var(--primary-font);
  font-size: 3rem;
  font-weight: bold;
  color: var(--secondary-color);
}

.project {
  height: 15rem;
  background-color: var(--bg-color-1);
  color: var(--primary-color);
  padding: 1rem 1.5rem;
  border-radius: 1rem;
  transition: 0.4s ease-in-out all;
}

.project:hover {
  transform: scale(1.09);
  border: 2px solid var(--secondary-color);
}

.project-title {
  display: block;
  font-size: 1.5rem;
  font-weight: bold;
  color: var(--secondary-color);
  text-align: center;
  margin-bottom: 0.5rem;
}

.project-desc {
  font-size: var(--primary-font-size);
  margin-bottom: 0.5rem;
}

.project a {
  text-decoration: none;
  color: var(--primary-color);
  font-size: 1.25rem;
  margin-right: 1rem;
}

.project a:hover {
  color: var(--secondary-color);
}

/* Media Queries */
@media screen and (max-width: 1100px) {
  .projects {
    grid-template-columns: repeat(2, 1fr);
  }
  .project:hover {
    transform: scale(1.06);
  }
}

@media screen and (max-width: 768px) {
  .projects {
    grid-template-columns: repeat(1, 1fr);
  }
  .project:hover {
    transform: scale(1.04);
  }
}

